/**
 * A hook that returns a UUID that lives for the life of the component.
 */
function useUUID() {
    const ref = useRef();
    if (ref.current === undefined) {
        ref.current = uuid(UUIDType.COMPONENT);
    }
    return ref.current;
}

export { useUUID };
